import React, { useEffect, useReducer, useRef } from 'react'
import { useStateContext } from './state'
// import usePrev from './hooks/usePrev'
import View from './view'
import './index.scss'

const TransitionViews = ({ location, children }) => {
  const [{ currentLocation, views }, dispatch] = useStateContext()
  // const prevLocation = usePrev(currentLocation)

  useEffect(() => {
    if (currentLocation !== location.pathname) {
      console.log('UPDATE_LOCATION')
      console.log(location.pathname)
      dispatch({
        type: 'UPDATE_LOCATION',
        location: location.pathname
      })
    }
  }, [location.pathname])

  useEffect(() => {
    if (currentLocation !== children.key) {
      console.log('ADD_VIEW')
      console.log(children.key)
      dispatch({
        type: 'ADD_VIEW',
        view: children
      })
    }
  }, [children.key])

  return (
    <div className='views'>
      {views.map((view, index) => {
        if (!view) return null
        console.log(view.key)
        return (
          <View
            key={view.key}
            view={view}
            action={!index ? 'enter' : 'leave'}
          />
        )
      })}
    </div>
  )
}

export default TransitionViews
